@import url(https://fonts.googleapis.com/css?family=Orbitron|Raleway|Roboto);
/* 開発中ホットリロードをするためにJS経由でStylusを読み込んでいる */
body {
  font-family: 'Orbitron', 'Roboto', 'Raleway', 'Open Sans', sans-serif !important;
}
.container {
  width: 100%;
}
nav {
  background-color: #13222e;
}
.brand-logo {
  height: 100%;
}
#brand-logo-img {
  width: 500px;
  height: 100%;
  background-image: url("/static/image_02.jpg");
  background-repeat: no-repeat;
  background-size: contain;
}
#section-hero {
  height: 600px;
  background-image: url("/static/image_01.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
.hero-heading {
  color: #eee;
  text-shadow: 4px 4px 0 #000;
}